String.prototype.replaceAll = function (s1: any, s2: any) {
  return this.replace(new RegExp(s1, 'gm'), s2);
};

export class DataUtil {

  public static replaceConfig(str: string, config: TemplateInterface): string {
    str = str.replaceAll(`{{productName}}`, config.productName || '')
      .replaceAll(`{{productDate}}`, config.productDate || '');

    return str;
  }
}
